import React, { Component } from 'react';
import {
  Card,
  CardBody,
  Button,
  FormGroup,
  Input,
} from 'reactstrap';
import './add-post.scss';

class AddPost extends Component {
  constructor(props){
    super(props);
    this.state = {
      text: '',
    }
    this.onChangeText = this.onChangeText.bind(this);
  }

  onChangeText(e) {
    this.setState({ text: e.target.value });
  }

  render() {
    const {
      text,
    } = this.state;
    return (
      <div className="add_post_cont shadow">
        <Card>
          <CardBody className="card_title">
            <div className="card_header">
              <p>Crear publicaci√≥n</p>
            </div>
          </CardBody>
          <CardBody>
            <form method="POST" className="add_post_form">
              <FormGroup>
                <Input onChange={this.onChangeText} value={text} className="text_post" type="textarea" />
              </FormGroup>
              <div className="form_post_footer">
                <Button type="submit" outline>Crear</Button>
              </div>
            </form>
          </CardBody>
        </Card>
      </div>
    )
  }
};

export default AddPost;
